import React from 'react';
import ReactDOM from 'react-dom';
import './header.css';
import Sidebar from './sidebar';

interface IProps {
}

interface IState {
  sidebarHidden: boolean;
}

class Header extends React.Component<IProps, IState> {
  constructor (props:any) {
    super(props);
    this.state = {sidebarHidden: true} as {sidebarHidden: boolean};
  }
  toggle = () => {
    this.setState((state) => ({
      sidebarHidden: !state.sidebarHidden,
    })
    );
  }
  render() {
    let classNameSidebar = 'sidebar';
    let classNameMenuToggle = 'menu-toggle';
    const sidebarHidden = this.state.sidebarHidden;
    if (sidebarHidden) {
      classNameSidebar += ' sidebar_hidden';
      classNameMenuToggle += ' menu-toggle_set-hidden';
    }
    return (
      <header className="header">
        <Sidebar class={classNameSidebar}/>
        <nav className={classNameMenuToggle} onClick={this.toggle}>
        </nav>
        <div className="switch">
          <div className="switch__handle"></div>
          <div className="switch__text">train</div>
        </div>
      </header>
    )
  }
}

export default Header;
