import React from 'react';
import './main.css';
import cards from './../../assets/cards';
import { Link } from 'react-router-dom';

const categoryList = cards[0].map((item, index) => {
  const arr = [...cards[index + 1]] as {word: string, translation: string, image: string, audioSrc: string}[];
  const addrImage = './' + arr[0].image;
  return (
    <Link to={"/main/" + (index + 1)} className="card" key = {item.toString()}>
      <img src={addrImage} alt={arr[0].word}></img>
      {item}
    </Link>
  );
})
const getGameList = (index: number) => {
  return (
    cards[index].map((item) => {
      const arr = item as {word: string, translation: string, image: string, audioSrc: string};
      const addrImage = '/' + arr.image;
      return (
        <div className="card" key = {arr.word}>
          <img src={process.env.PUBLIC_URL + addrImage} alt={arr.word}></img>
          {arr.word}
        </div>
      );
    })
  )
}
const Main = (props: any) => {
  const index = props.index;
  const gameList = getGameList(index);
  const list = props.index === '0' ? categoryList : gameList;
    return (
      <main className="main">
        {list}
      </main>
    )
  }

export default Main;
